import React from 'react'
import { Link } from 'react-router-dom'
import { Eventdata } from '../../Data/Shop'


function Ourevent() {
  return (
    <>
    <section className="our-event">
    <div className="container">
      <h1 data-aos="flip-left">Our Events</h1>
      <div className="event-one  d-flex" data-aos="fade-left" data-aos-offset={300} data-aos-easing="ease-in-sine">
       <div className='row'>
       
        {Eventdata.map((b)=>(
            
            <div className='col-lg-12 ' key={b}>
            <br></br>
            <div className="event-content ">
              <Link to="/events" className="mx-2">Wedding Party</Link>
              <i className="fa-solid fa-location-dot" /> 17 Main Street ,New York
              <h4>{b.title}</h4>
              <p data-aos="flip-left">{b.body4}</p>
              <img src={b.image} />
            </div>
          </div>

          
        ))}
       
      </div>
      </div>
      </div>
     
     
     
     
     
    
    
  </section> 
    </>
  )
}

export default Ourevent
