import React from 'react'
import { Routes, Route, Link } from "react-router-dom";
import About from '../../Pages/About';
import Blog from '../../Pages/Blog';
import Bookingsystem from '../../Pages/Bookingsystem';
import Contact from '../../Pages/Contact';
import Events from '../../Pages/Events';
import Gallery from '../../Pages/Gallery';
import Home from '../../Pages/Home';
import Loginform from '../../Pages/Loginform';
import Roomone from '../../Pages/Roomone';
import Roomonedetails from '../../Pages/Roomonedetails';
import Roomtwo from '../../Pages/Roomtwo';
import Roomtwodetails from '../../Pages/Roomtwodetails';
import Sigup from '../../Pages/Sigup';
import Staff from '../../Pages/Staff';



function Navbar() {
  return (
    <>
    
   <nav className="navbar navbar-expand-lg sticky-top">
  <div className="container">
    <a className="navbar-brand" href="#"><img src="https://storage.googleapis.com/theme-vessel-items/checking-sites/hotel-alpha-html/HTML/main/img/logos/logo.png" /></a>
    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
      <span className="navbar-toggler-icon" />
    </button>
    <div className="collapse navbar-collapse" id="navbarSupportedContent">
      <ul className="navbar-nav ms-auto mb-2 mb-lg-0">
        <li className="nav-item home">
          <Link className="nav-link active" aria-current="page" to="/">HOME</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/about">ABOUT US</Link>
        </li>
        <li className="nav-item dropdown">
          <Link className="nav-link dropdown-toggle" to="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            ROOMS
          </Link>
          <ul className="dropdown-menu">
            <li><Link className="dropdown-item" to="roomone">Rooms One</Link></li>
            <li><Link className="dropdown-item" to="/roomtwo">Rooms Two</Link></li>
            <li><Link className="dropdown-item" to="/roomonedetails">Rooms One Details</Link></li>
            <li><Link className="dropdown-item" to="/roomtwodetails">Rooms Two Details</Link></li>
          </ul>
        </li>
        <li className="nav-item dropdown">
          <Link className="nav-link dropdown-toggle" to="#" role="button" data-bs-toggle="dropdown" aria-expanded="false">
            PAGES
          </Link>
          <ul className="dropdown-menu">
            <li><Link className="dropdown-item" to="/gallery">Gallery</Link></li>
            <li><Link className="dropdown-item" to="/bookingsystem">Booking System</Link></li>
            <li><Link className="dropdown-item" to="/staff">Staff</Link></li>
            <li><Link className="dropdown-item" to="/events">Events</Link></li>
            <li><Link className="dropdown-item" to="/loginform">Login</Link></li>
            <li><Link className="dropdown-item" to="/sigup">Sigup</Link></li>
            <li><Link className="dropdown-item" to="">Forgot Password</Link></li>
          </ul>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/blog">BLOG</Link>
        </li>
        <li className="nav-item">
          <Link className="nav-link" to="/contact">CONTACT</Link>
        </li>
      </ul>
    </div>
  </div>
</nav>

<Routes>
<Route path="/" element={<Home />} />
<Route path="/about" element={<About />} />
<Route path="/roomone" element={<Roomone />} />
<Route path="/roomtwo" element={<Roomtwo />} />
<Route path="/roomonedetails" element={<Roomonedetails />} />
<Route path="/roomtwodetails" element={<Roomtwodetails />} />
<Route path="/gallery" element={<Gallery />} />
<Route path="/bookingsystem" element={<Bookingsystem />} />
<Route path="/staff" element={<Staff />} />
<Route path="/events" element={<Events/>} />
<Route path="/loginform" element={<Loginform/>} />
<Route path="/sigup" element={<Sigup/>} />
<Route path="/blog" element={<Blog/>} />
<Route path="/contact" element={<Contact/>} />





</Routes>





    </>
  )
}

export default Navbar
