import React from 'react'
import { Link } from 'react-router-dom'
import Workdata from '../../Data/Shop'


function Welcome() {
  return (
    <>
     
    <section className="welcome ">
    <div className="container ">
      <div className="row">
         {Workdata.map((item)=>(

            <div className="col-lg-6 wel-h " key={item.id} >
            <h6 data-aos="fade-up">WELCOME TO <span>ALPHA</span></h6>
            <h3 data-aos="fade-up">Finest Luxury Hotels &amp; Resort</h3>
            <p className="wel-p1" data-aos="fade-up">{item.body1}</p>
            <p className="wel-p1" data-aos="fade-down">{item.body2}</p>
            <Link to=" ">Vies Details</Link>
             </div>

         ))}
       
        <div className="col-lg-6 wel-img " data-aos="fade-right">
          <img src={require('../welcome1.jpg')} alt />
        </div>
      </div>
    </div>
  </section>
    </>
  )
}

export default Welcome
