import React from 'react'
import { Link } from 'react-router-dom'



function Header() {
  return (
    <>
     
    

    <header className="second-header  ">
  <div className="container ">
    <div className="row ">
      <div className="col col 6 ">
        <ul className="list-inline ">
          <li className="list-inline-item mx-2"><i className="fa-solid fa-phone mx-2"/><Link to="#" className="text-decoration-none a1">Need Support? +00-4X6-634-781</Link></li>
          <li className="list-inline-item"><i className="fa-solid fa-envelope mx-2"/><Link to="#" className="text-decoration-none a1">info@themevessel.com</Link></li>
        </ul>
      </div>
      <div className="col col 6  ">
        <ul className="list-unstyled list-inline  text-end ">
          <li className="list-inline-item"><Link to="" className="text-decoration-none"><i className="fa-brands fa-facebook-f" /></Link></li>
          <li className="list-inline-item"><Link to="" className="text-decoration-none"><i className="fa-brands fa-twitter" /></Link></li>
          <li className="list-inline-item"><Link to="" className="text-decoration-none"><i className="fa-brands fa-linkedin-in" /></Link></li>
          <li className="list-inline-item"><Link to="" className="text-decoration-none"><i className="fa-solid fa-magnifying-glass" /></Link></li>
          <Link to="/loginform" className="b1 text-decoration-none">Login</Link>
          <Link to="/sigup" className="b1 text-decoration-none">Register</Link>
        

            



          </ul>
        </div>
      </div>
    </div>

  </header>

 


    </>
  )
}

export default Header
