import React from 'react'
import { Link } from 'react-router-dom'

function Instruction() {
  return (
    <>

    <section className="instructor ">
  <div className="container">
    <div className="row mt-3">
      <div className="col-lg-6">
        <h3 data-aos="fade-right" data-aos-offset={300} data-aos-easing="ease-in-sine">Become An Instructor</h3>
        <p data-aos="fade-left" data-aos-offset={300} data-aos-easing="ease-in-sine">Join thousand of instructors and earn money hassle free</p>
      </div>
      <div className="col-lg-6 text-center mt-3" data-aos="fade-left">
        <Link to="/contact">Contact Now</Link>
      </div>
    </div>
  </div>
</section>

    </>
  )
}

export default Instruction
